#createC .left{
    width: 15%;
    margin-left: 6%;
    display: inline-block;
}

#createC .cover{
    margin-top: 55%;
    width: 100%;
    height: 30%;
    border: 0.2rem dashed #70e8f5;
    border-radius: 10%;
    text-align: center;
    font-size: 10rem;
    position: relative;
    background-size: 100% 100%;
}
#createC .character{
    margin-top: -25%;
    display: block;
}
#createC .coverTip{
    font-size: 1rem;
    margin-top: -15%;
}
#createC .coverFile{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    z-index: 1;
}
#createC .coverImg{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    font-size: 0.8rem;
}

#createC .life, #createC .dubbing, #createC .reading, #createC .ASMR, #createC .oralSkill{
    width: 41%;
    height: 6.5%;
    display: inline-block;
    border: none;
    color: black;
    text-align: center;
    font-size: 1.2rem;
    padding-top: 3%;
    cursor: pointer;
}
#createC .life{background: -webkit-gradient(linear, left top, right bottom, from(#f09619), to(#fe562d));background: -webkit-linear-gradient(top left, #f09619, #fe562d);background: -o-linear-gradient(top left, #f09619, #fe562d);background: linear-gradient(to bottom right, #f09619, #fe562d);}
#createC .dubbing{background: -webkit-gradient(linear, left top, right bottom, from(#fffed6), to(#fffc14));background: -webkit-linear-gradient(top left, #fffed6, #fffc14);background: -o-linear-gradient(top left, #fffed6, #fffc14);background: linear-gradient(to bottom right, #fffed6, #fffc14);}
#createC .reading{background: -webkit-gradient(linear, left top, right bottom, from(#00b4db), to(#0087b3));background: -webkit-linear-gradient(top left, #00b4db, #0087b3);background: -o-linear-gradient(top left, #00b4db, #0087b3);background: linear-gradient(to bottom right, #00b4db, #0087b3);}
#createC .ASMR{background: -webkit-gradient(linear, left top, right bottom, from(#a6ff7b), to(#7effcb));background: -webkit-linear-gradient(top left, #a6ff7b, #7effcb);background: -o-linear-gradient(top left, #a6ff7b, #7effcb);background: linear-gradient(to bottom right, #a6ff7b, #7effcb);}
#createC .oralSkill{background: -webkit-gradient(linear, left top, right bottom, from(#e1a8c6), to(#6f55a6));background: -webkit-linear-gradient(top left, #e1a8c6, #6f55a6);background: -o-linear-gradient(top left, #e1a8c6, #6f55a6);background: linear-gradient(to bottom right, #e1a8c6, #6f55a6);}
#createC .life, #createC .reading, #createC .oralSkill{
    margin-left: 2%;
}
#createC .dubbing, #createC .ASMR{
    margin-right: 2%;
}

#createC .dubbing, #createC .ASMR{
    margin-left: 10%;
}

#createC .life, #createC .dubbing{
    margin-top: 20%;
}

#createC .reading, #createC .ASMR, #createC .oralSkill{
    margin-top: 5%;
}

#createC .visibilityLead, #createC .visibility{
    font-size: 1.6rem;
    margin-left: 2%;
    margin-top: 6%;
    display: block;
}

#createC .visibility{
    width: 93%;
    height: 5%;
    font-size: 0.9rem;
    border: 0.1rem solid #70e8f5;
    padding-top: 2%;
    padding-left: 7%;
    margin-top: -3%;
    position: relative;
}
#createC .selection{
    font-size: 0.9rem;
    margin-left: 2%;
    padding-left: 7%;
    width: 93%;
    display: block;
    cursor: pointer;
    opacity: 0;
    background-size: 100% 100%;
    -webkit-transition: background-color 0.5s, opacity 0.2s;
    -o-transition: background-color 0.5s, opacity 0.2s;
    transition: background-color 0.5s, opacity 0.2s;
}


#createC .down{
    font-size: 2rem;
    border-left: 0.1rem solid #70e8f5;
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
}

#createC .right{
    margin-left: 8%;
    width: 67%;
    display: inline-block;
    position: relative;
}

#createC .productions{
    height: 55%;
    position: relative;
}
#createC .addedLead, #createC .myLead{
    margin-top: 10%;
    display: inline-block;
    font-size: 1.1rem;
    margin-bottom: 2%;
}
#createC .addedLead{
    margin-right: 54%;
}
#createC .added, #createC .my{
    height: 60%;
    width: 60%;
    display: inline-block;
    scroll-behavior: smooth;
}
#createC .my{
    width: 30%;
}
#createC .one, #createC .myOne{
    display: inline-block;
    margin-bottom: 2%;
    height: 13%;
    width: 45%;
    position: relative;
    margin-right: 5%;
    white-space: nowrap;
}
#createC .myOne{
    width: 90%;
}
#createC .oneImg{
    width: 14%;
    height: 100%;
}
#createC .oneName{
    display: inline-block;
    margin-left: 10%;
    width: 50%;
    font-size: 1.2rem;
}
#createC .oneOperation{
    position: absolute;
    right: 0;
    font-size: 3rem;
    top: -60%;
    color: red;
    cursor: pointer;
}
#createC .my .oneOperation{
    color: green;
    font-size: 2.5rem;
    top: -40%;
}
#createC .barBack{
    width: 0.5%;
    height: 60%;
    display: inline-block;
    background-color: grey;
    margin-right: 4.1%;
}

#createC .otherInfo{
    height: 45%;
    margin-top: -1%;
    position: relative;
}
#createC .titleLead, #createC .title, #createC .labelLead, #createC .label, #createC .descriptionLead, #createC .description{
    font-size: 1.5rem;
}
#createC .title, #createC .label, #createC .description{
    background-color: #343a40;
}
#createC .title, #createC .label{
    border: 0.07rem solid #343a40;
    border-bottom: 0.09rem solid #70e8f5;
}
#createC .title{
    width: 85.4%;
}
#createC .label{
    width: 90%;
}
#createC .description{
    width: 85%;
    height: 52%;
    border: 0.09rem solid #70e8f5;
    resize: none;
}
#createC .finish{
    position: absolute;
    left: 87%;
    top: 35.5%;
    width: 10%;
    height: 52%;
    border: 0.09rem solid #70e8f5;
    font-size: 2.5rem;
    color: #343a40;
    background: -webkit-gradient(linear, left top, left bottom, from(#70e8f5), to(green));
    background: -webkit-linear-gradient(top, #70e8f5, green);
    background: -o-linear-gradient(top, #70e8f5, green);
    background: linear-gradient(to bottom, #70e8f5, green);
}

#createC .display{
    margin-top: 10%;
    width: 80%;
}


