
.bottomScroll{
    position: fixed;
    bottom: -3%;
    width: 100%;
    height: 10%;
    overflow: hidden;
    scroll-behavior: smooth;
    cursor: pointer;
}
.bottomScrollImage{
    height: 50%;
}
.bottomPoint{
    position: fixed;
    bottom: 0;
    height: 3.3%;
    width: 1%;
    cursor: pointer;
}
#mainV2{
    white-space: nowrap;
}
#mainV2 .bottomPointImage{
    height: 100%;
    width: 100%;
}

/*.mainTitle{
    position: fixed;
    left: 10%;
    height: 100%;
    width: 90%;
    z-index: 0;
}*/


#mainV2 .title{
    width: 46%;
    margin-left: 7%;
    -webkit-transition: all 2s;
    -o-transition: all 2s;
    transition: all 2s;
}

.microphone{
    width: 30%;
    opacity: 0.1;
    margin-top: 10%;
    margin-left: 2%;
}

.mainVoice, .mainActivity, .contactUs{
    font-size: 5rem;
    padding: 0 2%;
    /*padding-left: 3%;
    padding-right: 1%;
    padding-top: 1%;
    padding-bottom: 3%;*/
    width: 10%;
    height: 52%;
    display: inline-block;
    border: 3px solid #70e8f5;
    margin-bottom: 15%;
}

#mainV2 .carousel{
    position: relative;
    margin-bottom: 5%;
    margin-left: 4%;
    height: 75%;
    width: 20%;
    display: inline-block;
}
#mainV2 .carouselCover{

    width: 100%;
    height: 100%;
}
#mainV2 .cClassZero, #mainV2 .cClassOne, #mainV2 .cClassTwo, #mainV2 .cClassThree, #mainV2 .cClassFour{
    position: absolute;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
}
#mainV2 .cClassZero, #mainV2 .cClassFour{
    top: 0;
    left: 16%;
    width: 68%;
    height: 48%;
    background-color: #fffe95;
    z-index: 0;
}
#mainV2 .cClassOne, #mainV2 .cClassThree{
    top: 10%;
    left: 8%;
    width: 82%;
    height: 54%;
    background-color: #fe562d;
    z-index: 1;
}
#mainV2 .cClassTwo{
    top: 20%;
    left: 0;
    width: 100%;
    height: 60%;
    background-color: #00b4db;
    z-index: 2;
}
#mainV2 .cClassThree{
    top: 36%;
    background-color: white;
}
#mainV2 .cClassFour{
    top: 52%;
    background-color: #7effcb;
}

.mainActivity, .contactUs{
    margin-left: 20%;
    font-size: 3.5rem;
    padding: 0 3%;
}

.mainCircle{
    margin-left: 3%;
    width: 18%;
    height: 80%;
    display: inline-block;
    margin-bottom: 3%;
    overflow: hidden;

}

.mainCircleLead{
    width: 100%;
    height: 18%;
    margin-bottom: .5%;
    margin-right: 1%;
    display: inline-block;
    background-size: 100% 100%;
    background-color: #00b4db;
}

.mainCircleContent{
    margin-right: 2%;
    margin-bottom: 0.3%;
    width: 49%;
    height: 18%;
    background-image: url("../images/404.png");
    background-size: 100% 100%;
    display: inline-block;
    position: relative;
}

.mainCircleBottom{
    width: 100%;
    height: 20%;
    background-color: #343a40;
    opacity: 0.5;
    position: absolute;
    bottom: 0;
}

.mainCircleName{
    width: 100%;
    height: 20%;
    position: absolute;
    bottom: 0;
    text-align: center;
    font-size: 0.8rem;
    color: white;
    text-shadow: 0 0 0.2rem black;
    z-index: 1;
}



.mainCircleLead, .mainCircleContent{
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;

}

.mainCircleLead:hover, .mainCircleContent:hover{
    transform: rotateY(50deg);
    -webkit-transform: rotateY(50deg);	/* Safari 和 Chrome */
    -moz-transform: rotateY(50deg);	/* Firefox */

    transform-origin:50% 50%;
    -ms-transform-origin:50% 50%; 		/* IE 9 */
    -webkit-transform-origin:50% 50%;	/* Safari 和 Chrome */
    -moz-transform-origin:50% 50%;		/* Firefox */
    -o-transform-origin:50% 50%;		/* Opera */
}

.mainActivitiesItemFirst, .mainActivitiesItem{
    width: 22%;
    height: 70%;
    color: black;
    padding-top: 5%;
    font-size: 3rem;
    text-align: center;
    display: inline-block;
    margin-bottom: 6.8%;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    background-image: url("../images/404b.png");
    background-size: 100% 100%;
}

.mainActivitiesItemFirst{
    margin-left: 3%;
}

.mainActivitiesItem{
    -webkit-box-shadow: -1px 0 5px #343a40;
            box-shadow: -1px 0 5px #343a40;
}

.mainActivitiesItemFirst:hover, .mainActivitiesItem:hover{
    width: 30%;
    margin-right: 1%;
}

.contactContent{
    width: 88%;
    text-align: center;
    display: inline-block;
    font-size: 6rem;
    padding-top: 17%;
}

#Ving .hiddenBtn{
    position: absolute;
    left: -2%;
    top: 0;
    display: inline-block;
    font-size: 1.1rem;
    opacity: 0;
}

#mainV2 .carouselName{
    font-size: 2rem;
}


