#PCenterV2{
    width: 95%;
    height: 87%;
    position: absolute;
    right: 0;
    bottom: 0;
}

#PCenterV2 .avatar{
    display: inline-block;
    width: 8%;
    height: 17%;
    background-image: url("../images/helloSingleDog.png");
    background-size: 100% 100%;
    border-radius: 50%;
}
#PCenterV2 .nameSign{
    position: absolute;
    left: 13%;
    top: 5%;
    color: #fffe95;
    white-space: nowrap;
}
#PCenterV2 .userName{
    font-size: 1.5rem;
}
#PCenterV2 .more{
    margin-left: 15%;
    font-size: 0.8rem;
    padding-bottom: 0.5%;
    border-bottom: 0.09rem solid #fffe95;
    cursor: pointer;
}
#PCenterV2 .doCare{
    margin-left: 15%;
    font-size: 1.2rem;
    border-bottom: 0.1rem solid #fffe95;
    cursor: pointer;
}
#PCenterV2 .productLead, #PCenterV2 .collectLead{
    display: inline-block;
    margin-top: 2%;
    margin-bottom: 1%;
    cursor: pointer;
}
#PCenterV2 .productLead{
    font-size: 1.3rem;
    margin-right: 1%;
    padding-left: 0.5%;
}
#PCenterV2 .collectLead{
    font-size: 1.1rem;
    padding-left: 0.5%;
}
#PCenterV2 .container{
    width: 55%;
    white-space: nowrap;
}
#PCenterV2 .collect, #PCenterV2 .product{
    display: inline-block;
    height: 65%;
    scroll-behavior: smooth;
}
#PCenterV2 .collect{

}
#PCenterV2 .one{
    height: 12.3%;
    white-space: nowrap;
    color: #fffe95;
}
#PCenterV2 .cover{
    height: 100%;
    margin-top: -5%;
}
#PCenterV2 .nameCircleTime{
    width: 30%;
    display: inline-block;
    margin-left: 4%;
}
#PCenterV2 .name{
    font-size: 1.2rem;
    margin-right: 10%;
    cursor: pointer;
}
#PCenterV2 .circle{
    font-size: 0.5rem;
    padding: 1% 2%;
    border: 0.09rem solid #70e8f5;
    border-radius: 25px 25px;
}
#PCenterV2 .time{
    font-size: 0.8rem;
}
#PCenterV2 .record, #PCenterV2 .operation{
    padding-top: 3%;
}
#PCenterV2 .record{
    width: 37%;
    display: inline-block;
    font-size: 0.8rem;
}
#PCenterV2 .like, #PCenterV2 .comment, #PCenterV2 .add{
    width: 7%;
    height: 60%;
    display: inline-block;
    margin-top: -1%;
}
#PCenterV2 .operation{
    width: 20%;
    display: inline-block;
    font-size: 0.8rem;
    cursor: pointer;
}
#PCenterV2 .edit, #PCenterV2 .remove{
    margin-right: 10%;
}
#PCenterV2 .separator{
    width: 92.5%;
    height: 0.2%;
    background-color: black;
    margin-top: 2%;
    margin-left: 2%;
    margin-bottom: 2%;
}

#PCenterV2 .collect .nameCircleTime{
    width: 50%;
    white-space: nowrap;
    display: inline-block;
    padding-top: 1%;
}
#PCenterV2 .collect .name{
    margin-right:5%;
}
#PCenterV2 .collect .time{
    margin-left: 5%;
    width: 20%;
}
#PCenterV2 .collect .add{
    width: 20%;
}
#PCenterV2 .collect .record{
    width: 10%;
    margin-left: 5%;
}
#PCenterV2 .collect .record, #PCenterV2 .collect .operation{
    padding-top: 1.8%;
}
#PCenterV2 .collect .edit{
    margin-left: 15%;
}

#PCenterV2 .right{
    position: absolute;
    right: 0;
    top: 0;
    width: 23%;
    overflow: visible;
}
#PCenterV2 .fans, #PCenterV2 .care, #PCenterV2 .proNumber, #PCenterV2 .colNumber, #PCenterV2 .likeNumber{
    margin-top: 15%;
    height: 10%;
    position: relative;
    background: -webkit-gradient(linear, left top, right top, from(#e1a8c6), to(#6f55a6));
    background: -webkit-linear-gradient(left, #e1a8c6, #6f55a6);
    background: -o-linear-gradient(left, #e1a8c6, #6f55a6);
    background: linear-gradient(to right, #e1a8c6, #6f55a6);
    color: #343a40;
    font-size: 1.1rem;
}
#PCenterV2 .fans, #PCenterV2 .care{
    cursor: pointer;
}
#PCenterV2 .care{
    background: -webkit-gradient(linear, left top, right top, from(#00b4db), to(#0087b3));
    background: -webkit-linear-gradient(left, #00b4db, #0087b3);
    background: -o-linear-gradient(left, #00b4db, #0087b3);
    background: linear-gradient(to right, #00b4db, #0087b3);
}
#PCenterV2 .proNumber{
    background: -webkit-gradient(linear, left top, right top, from(#a6ff7b), to(#7effcb));
    background: -webkit-linear-gradient(left, #a6ff7b, #7effcb);
    background: -o-linear-gradient(left, #a6ff7b, #7effcb);
    background: linear-gradient(to right, #a6ff7b, #7effcb);
}
#PCenterV2 .colNumber{
    background: -webkit-gradient(linear, left top, right top, from(#fffed6), to(#fffc14));
    background: -webkit-linear-gradient(left, #fffed6, #fffc14);
    background: -o-linear-gradient(left, #fffed6, #fffc14);
    background: linear-gradient(to right, #fffed6, #fffc14);
}
#PCenterV2 .likeNumber{
    background: -webkit-gradient(linear, left top, right top, from(#f09619), to(#fe562d));
    background: -webkit-linear-gradient(left, #f09619, #fe562d);
    background: -o-linear-gradient(left, #f09619, #fe562d);
    background: linear-gradient(to right, #f09619, #fe562d);
}
#PCenterV2 .first{
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    position: absolute;
    left: -11%;
    top: -70%;
    width: 20%;
    height: 200%;
    background-color: #343a40;
}
#PCenterV2 .word, #PCenterV2 .number{
    position: absolute;
    bottom: 2%;
    left: 15%;
}
#PCenterV2 .number{
    left: 35%;
    bottom: -16%;
    font-size: 3rem;
}

#PCenterV2 .myCares{
    background-color: grey;
    position: absolute;
    top: -90%;
    left: 32%;
    width: 35%;
    height: 90%;
    border-radius: 0.5rem;
    overflow-y: initial;
}
#PCenterV2 .caresLead{
    background-color: #fe562d;
    height: 8%;
    color: white;
    font-size: 1rem;
    padding-top: 1%;
    position: relative;
}
#PCenterV2 .careLWord, #PCenterV2 .closeCare{
    top: 15%;
    font-size: 1.3rem;
    position: absolute;
}
#PCenterV2 .careLWord{
    left: 3%;
}
#PCenterV2 .closeCare{
    right: 4%;
    cursor: pointer;
}
#PCenterV2 .caresSep{
    height: 1%;
}
#PCenterV2 .oneCare{
    height: 10%;
    background-color: white;
    color: black;
    padding-top: 2%;
    margin-bottom: 1.5%;
    -webkit-box-shadow: 0 0 10px;
            box-shadow: 0 0 10px;
}
#PCenterV2 .careAvatar{
    margin-left: 3%;
}
#PCenterV2 .careName, #PCenterV2 .careSign{
    display: inline-block;
}
#PCenterV2 .careName{
    width: 20%;
    margin-left: 3%;
    margin-right: 2%;
    font-size: 1rem;
    cursor: pointer;
}
#PCenterV2 .careSign{
    width: 45%;
    margin-right: 5%;
    font-size: 0.6rem;
}
#PCenterV2 .cancelCare{
    padding: 0.5% 1.5%;
    border: 0.1rem solid #fe562d;
    border-radius: 25px;
    font-size: 1rem;
    cursor: pointer;
}


#PCenterV2 .editContainer{
    height: 91%;
    background: -webkit-gradient(linear, left top, left bottom, from(#127284), to(#2a254d));
    background: -webkit-linear-gradient(top, #127284, #2a254d);
    background: -o-linear-gradient(top, #127284, #2a254d);
    background: linear-gradient(to bottom, #127284, #2a254d);
    color: white;
    font-size: 2rem;
    padding: 13% 5%;
}
#PCenterV2 .editName, #PCenterV2 .editLabel, #PCenterV2 .editDes{
    border: white;
    background-color: rgba(0,0,0,0);
    border-bottom: 0.09rem solid white;
    width: 70%;
    display: inline-block;
    font-size: 1.3rem;
}
#PCenterV2 .editDes{
    border: 0.09rem solid white;
    width: 100%;
    height: 30%;
    resize: none;
}
#PCenterV2 .editFinish{
    border: 0.2rem solid white;
    background-color: rgba(0,0,0,0);
    width: 90%;
    margin-left: 5%;
    margin-top: 8%;
    padding: 1% 1%;
    border-radius: 30px;
}
#PCenterV2 .colProOne{
    height: 6%;
    width: 110%;
    margin-bottom: 2%;
}
#PCenterV2 .corProName{
    font-size: 0.8rem;
}
#PCenterV2 .colProCir{
    font-size: 0.5rem;
}
#PCenterV2 .colProTime{
    font-size: 0.4rem;
    display: block;
}
#PCenterV2 .colProRecord{

}
#PCenterV2 .colProOp{
    cursor: initial;
}


