
.coreDisplay, .progressBack, .progress, .productTime, .actionIcon, .commentDepartment, #playPage .waitTipm, #playPage .myCollect{
    position: fixed;
}
#playPage .myCollect{
    background-color: grey;
    top: -80%;
    left: 32%;
    width: 35%;
    height: 72%;
    border-radius: 0.5rem;
    overflow-y: scroll;

}
#playPage .collectLead{
    background-color: #fe562d;
    height: 8%;
    color: white;
    font-size: 1rem;
    padding-top: 1%;
    position: relative;
}
#playPage .collectLWord, #playPage .closeCollect{
    top: 15%;
    font-size: 1.3rem;
    position: absolute;
}
#playPage .collectLWord{
    left: 3%;
}
#playPage .closeCollect{
    right: 4%;
    cursor: pointer;
}
#playPage .collectSep{
    height: 1%;
}
#playPage .oneCollect{
    height: 10%;
    background-color: white;
    color: black;
    padding-top: 2%;
    margin-bottom: 1.5%;
    -webkit-box-shadow: 0 0 10px;
            box-shadow: 0 0 10px;
}
#playPage .oneCollect{
    height: 12.3%;
    white-space: nowrap;
    color: black;
}
#playPage .coverCollect{
    height: 100%;
    margin-top: -8%;
}
#playPage .nameCircleTime{
    width: 45%;
    display: inline-block;
    margin-left: 6%;
    padding-top: 2%;
}
#playPage .nameCollect{
    font-size: 1.2rem;
    margin-right: 6%;
}
#playPage .circleCollect{
    font-size: 0.7rem;
    padding: 1% 2%;
    border: 0.09rem solid #70e8f5;
    border-radius: 25px 25px;
}
#playPage .recordCollect, #playPage .operationCollect{
    padding-top: 3%;
}
#playPage .recordCollect{
    width: 25%;
    display: inline-block;
    font-size: 0.8rem;
}
#playPage .addCollect{
    width: 25%;
    height: 60%;
    display: inline-block;
    margin-top: -1%;
}
#playPage .operationCollect{
    width: 20%;
    display: inline-block;
    font-size: 1rem;
    cursor: pointer;
}
#playPage .editCollect{
    margin-right: 10%;
    border: 0.1rem solid #fe562d;
    border-radius: 25px;
    padding: 5% 10%;
}
#playPage .separatorCollect{
    width: 92.5%;
    height: 0.2%;
    background-color: black;
    margin-top: 2%;
    margin-left: 2%;
    margin-bottom: 2%;
}

#playPage .waitTip{
    top: 30%;
    left: 20%;
    font-size: 1.6rem;
    z-index: 10;
}
.coreDisplay{
    text-align: center;
    width: 70%;
    height: 100%;
    top: 15%;
    overflow: visible;
}

.bar{
    height: 35%;
}
.star{
    height: 100%;
    margin-top: -10%;
}
#canvas{
    height: 100%;
}

/*.voicePicture{
    height: 33%;
    width: 22.5%;
    margin-bottom: 50%;
    display: block;
}*/



#playPage .productInfo{
    position: absolute;
    width: 100%;
    top: 43.5%;
    display: block;
    white-space: nowrap;
}
.productName{
    font-size: 2.5rem;
    position: relative;
    z-index: 10;
    display: inline-block;
}

.productCircle{
    font-size: 1rem;
    margin-left: 5%;
    position: relative;
    display: inline-block;
    z-index: 1;
    border:1px solid;
    border-radius:25px;
    -moz-border-radius: 25px;/* Old Firefox */
    -webkit-border-radius: 25px;
    padding: 0.25%;
}


.authorName{
    font-size: 2rem;
    cursor: pointer;
}

#playPage .progressBack{
    top: 75%;
    left: 9%;
    width: 52%;
    height: 1%;
    background-color: black;
    cursor: pointer;
    border:0.07rem solid;
    border-radius:25px;
    -moz-border-radius:25px; /* Old Firefox */
}

#playPage .progress{
    top: 75%;
    left: 9%;
    width: 0;
    height: 1%;
    background-color: #70e8f5;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    cursor: pointer;
    border:0.07rem solid;
    border-radius:25px;
    -moz-border-radius:25px; /* Old Firefox */
}

.productTime{
    top: 77%;
    left: 9%;
}

.totalLength{
    margin-left: 48%;
}

.actionIcon{
    top: 80%;
    left: 8.8%;
}

.audienceActionFirst{
    font-size: 2rem;
    height: 5%;
    width: 4%;
    display: inline-block;
    margin-top: 1%;
}

.actionIcon .rateSelect{
    position: fixed;
    width: 4%;
    height: 19.5%;
    top: 63%;
    left: 8.8%;
    font-size: 2rem;
    background-color: #343a40;
    display: none;
}

.audienceAction, .audiencePlay{
    margin-left: 5%;
    width: 3%;
    height: 6%;
    margin-top: -1%;
}

.changeAEffect{
    margin-left: 5%;
    font-size: 2.4rem;
    cursor: pointer;
}

.audienceActionFirst, .audienceAction, .audiencePlay{
    cursor: pointer;
}
#playPage .switch{
    font-size: 2rem;
}

.audiencePlay{
    width: 3%;
    height: 7%;
}

.commentDepartment{
    top: 15%;
    left: 70%;
    border-left: 0.07rem solid black;
    width: 30%;
    height: 72%;
    overflow-y: hidden;
    scroll-behavior: smooth;
}

.totalComments, #playPage .descriptionLead, #playPage .description{
    font-size: 1.5rem;
    margin-left: 7%;
}
#playPage .descriptionLead{
    cursor: pointer;
}
#playPage .description{
    display: block;
    height: 0;
    overflow: hidden;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}
#playPage .descriptionLead:hover{
    border-bottom: 0.1rem solid #70e8f5;
}

#playPage .comment{
    display: block;
    position: relative;
    margin-left: 8%;
    font-size: 1.2rem;
    margin-top: 5%;
}
.commentContent{
    width: 80%;
}
.doComment{
    position: absolute;
    left: 70%;
    top: 87%;
    border-left: 0.07rem solid black;
    width: 30%;
    height: 10%;
}

.commentatorAvatar{
    margin-right: 5%;
}
#playPage .loginAvatar, #playPage .typeArea{
    margin-left: 8%;
    margin-right: 5%;
}
#playPage .typeArea{
    margin-right: 2.5%;
    border: 0.07rem solid #343a40;
    border-bottom: 0.1rem solid #70e8f5;
    background-color: #343a40;
    width: 70%;
}

.commentContent{
    display: block;
    margin-left: 16%;
}

.timeAndLikes{
    margin-left: 50%;
    font-size: 1rem;
}

.showLike{
    display: inline-block;
    width: 6%;
    height: 6%;
    margin-left: 10%;
    margin-top: -1%;
    margin-right: 2%;
}

.separateBar{
    width: 90%;
    height: 0.1rem;
    margin-left: 5%;
    margin-top: 2%;
    background-color: #222222;
}

#playPage .submit{
    font-size: 2rem;
}


