
#OneCircle .productResult{
    white-space: nowrap;
    scroll-behavior: smooth;
}

.topNavigateOther{
    opacity: 1;
}

#OneCircle .productsResult, #OneCircle .productResult, #OneCircle .users{
    display: inline-block;
}

#OneCircle .productResultLead, #OneCircle .productsResultLead, #OneCircle .usersLead{
    font-size: 4rem;
    height: 50%;
    width: 5%;
    margin-left: 5%;
    border-right: 1px solid #70e8f5;
    display: inline-block;
    margin-bottom: 14%;
}

#OneCircle .productResultContainer{
    display: inline-block;
    width: 80%;
    height: 80%;
    margin-top: 7%;
    margin-left: 5%;
}

#OneCircle .productResultContents{
    width: 100%;
    height: 30%;
    margin-bottom: 0.5%;
    display: inline-block;
}

#OneCircle .productsResultContents{
    width: 25%;
    height: 100%;
    margin-right: 0;
    display: inline-block;
}

#OneCircle .productResultContent{
    width: 15%;
    margin-right: 1%;
    display: inline-block;
    position: relative;
    background-color: #70e8f5;
    background-size: 100% 100%;
}

#OneCircle .contentAndDecoration{
    height: 30.3%;
    margin-bottom: 3.6%;
    position: relative;
}

#OneCircle .productsResultContent{
    width: 60%;
    height: 100%;
    position: relative;
    background-color: #70e8f5;
    display: inline-block;
    z-index: 1;
    background-size: 100% 100%;
}

#OneCircle .productsDecoration{
    position: absolute;
    left: 55%;
    width: 39%;
    height: 100%;
}

#OneCircle .productResultBottom, #OneCircle .productsResultBottom{
    width: 100%;
    height: 20%;
    background-color: #343a40;
    opacity: 0.4;
    position: absolute;
    bottom: 0;
    z-index: 0;
}

#OneCircle .productResultName{
    color: white;
    width: 100%;
    height: 20%;
    position: absolute;
    bottom: 0;
    text-align: center;
    font-size: 1.3rem;
    z-index: 1;
}


