#create .left{
    width: 15%;
    margin-left: 6%;
    display: inline-block;
}

#create .cover{
    margin-top: 55%;
    width: 100%;
    height: 30%;
    border: 0.2rem dashed #70e8f5;
    border-radius: 10%;
    text-align: center;
    font-size: 10rem;
    position: relative;
    background-size: 100% 100%;
}
#create .character{
    margin-top: -25%;
    display: block;
}
#create .coverTip{
    font-size: 1rem;
    margin-top: -15%;
}
#create .coverFile{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    z-index: 1;
}
#create .coverImg{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    font-size: 0.8rem;
}

#create .life, #create .dubbing, #create .reading, #create .ASMR, #create .oralSkill{
    width: 41%;
    height: 6.5%;
    display: inline-block;
    border: 0.1rem solid #70e8f5;
    text-align: center;
    font-size: 1.2rem;
    padding-top: 3%;
    cursor: pointer;
}
#create .life, #create .reading, #create .oralSkill{
    margin-left: 2%;
}
#create .dubbing, #create .ASMR{
    margin-right: 2%;
}

#create .dubbing, #create .ASMR{
    margin-left: 10%;
}

#create .life, #create .dubbing{
    margin-top: 20%;
}

#create .reading, #create .ASMR, #create .oralSkill{
    margin-top: 5%;
}

#create .visibilityLead, #create .visibility{
    font-size: 1.6rem;
    margin-left: 2%;
    margin-top: 6%;
    display: block;
}

#create .visibility{
    width: 93%;
    height: 5%;
    font-size: 0.9rem;
    border: 0.1rem solid #70e8f5;
    padding-top: 2%;
    padding-left: 7%;
    margin-top: -3%;
    position: relative;
}
#create .selection{
    font-size: 0.9rem;
    margin-left: 2%;
    padding-left: 7%;
    width: 93%;
    display: block;
    cursor: pointer;
    opacity: 0;
    background-size: 100% 100%;
    -webkit-transition: background-color 0.5s, opacity 0.2s;
    -o-transition: background-color 0.5s, opacity 0.2s;
    transition: background-color 0.5s, opacity 0.2s;
}


#create .down{
    font-size: 2rem;
    border-left: 0.1rem solid #70e8f5;
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
}

#create .right{
    margin-left: 8%;
    width: 67%;
    display: inline-block;
    position: relative;
}

#create .uploadAndRecord, #create .previewTailor{
    height: 55%;
    position: relative;
}
#create .uploadImg, #create .upload, #create .recordImg, #create .record, #create .upFile, #create .recordTime{
    position: absolute;
    cursor: pointer;
}
#create .upFile{
    z-index: 1;
}
#create .uploadImg, #create .recordImg{
    width: 6%;
}
#create .uploadImg, #create .upFile, #create .recordTime{
    left: 12%;
    top: 47%;
}
#create .upload{
    left: 22%;
    top: 50%;
}
#create .upFile, #create .recordTime{
    opacity: 0;
    height: 15%;
}
#create .recordImg{
    left: 56%;
    top: 47%;
}
#create .record{
    left: 66%;
    top: 50%;
}
#create .recordTime{
    font-size: 5rem;
    top: 40%;
    width: 27%;
    height: 30%;
    display: block;
    cursor: initial;
}

#create .preview, #create .operation, #create .progressBack, #create .progress, #create .userLength{
    position: absolute;
}
#create .preview{
    top: 20%;
    left: 0;
    width: 70%;
    height: 60%;
}
#create .operation{
    width: 25%;
    top: 30%;
    left: 75%;
    font-size: 1.8rem;
    z-index: 1;
}
#create .reRecordIcon, #create .upIcon, #create .addIcon{
    font-size: 2.2rem;
    margin-left: 10%;
    margin-right: 10%;
}
#create .addIcon{
    margin-top: 10%;
}
#create .playIcon, #create .pauseIcon{
    font-size: 3rem;
    margin-left: 6.5%;
    margin-right: 9.2%;
}
#create .play, #create .reRecord, #create .upInOperation, #create .addBack{
    cursor: pointer;
}
#create .play, #create .playIcon, #create .reRecord, #create .reRecordIcon, #create .pauseIcon, #create .addBack, #create .addIcon{
    margin-bottom: 15.6%;
}
#create .play, #create .playIcon, #create .pauseIcon{
    margin-top: 0;
}
#create .play{
    position: relative;
}
#create .pause{
    position: absolute;
    bottom: 10%;
    left: 97%;
    white-space: nowrap;
}
#create .progressBack{
    left: 0;
    top: 85%;
    width: 70%;
    height: 2.5%;
    cursor: pointer;
    border: 0.1rem solid;
    border-radius:25px;
    z-index: 1;
}
#create .progress{
    left: 0;
    top: 85%;
    width: 0;
    height: 2.5%;
    background-color: #70e8f5;
    background-size: 100% 100%;
    border-radius:25px;
}
#create .userLength{
    left: 0;
    top: 85%;
    width: 70%;
    height: 2.5%;
    border: 0.07rem solid;
    border-radius:25px;
}
#create .arrowLeft, #create .arrowRight{
    position: absolute;
    left: 0;
    top: 80%;
    height: 5%;
}
#create .arrowRight{
    left: 69.2%;
}
#create .bottomTime{
    height: 8%;
    position: absolute;
    left: 0;
    top: 87%;
    font-size: 1rem;
}
#create .now{
    margin-left: 30%;
}
#create .userEnd{
    margin-left: 30.6%;
}

#create .otherInfo{
    height: 45%;
    margin-top: -1%;
    position: relative;
}
#create .titleLead, #create .title, #create .labelLead, #create .label, #create .descriptionLead, #create .description, #create .upload, #create .record{
    font-size: 1.5rem;
}
#create .title, #create .label, #create .description{
    background-color: #343a40;
}
#create .title, #create .label{
    border: 0.07rem solid #343a40;
    border-bottom: 0.09rem solid #70e8f5;
}
#create .title{
    width: 85.4%;
}
#create .label{
    width: 90%;
}
#create .description{
    width: 85%;
    height: 52%;
    border: 0.09rem solid #70e8f5;
    resize: none;
}
#create .finish{
    position: absolute;
    left: 87%;
    top: 35.5%;
    width: 10%;
    height: 52%;
    border: 0.09rem solid #70e8f5;
    font-size: 2.5rem;
    color: #343a40;
    background-color: #70e8f5;
}

#create .display{
    margin-top: 10%;
    width: 80%;
}


