html, body{
    width: 100%;
    height: 100%;
    font-size: 16px;
}

div{
    width: 100%;
    height: 100%;
    overflow:hidden;
    font-size: 100%;
}

.Ving{
    background: #343a40;
    color: #70e8f5;
}

.messageTip{
    display: block;
    width: 100%;
    text-align: center;
    position: fixed;
    top: 2%;
    -webkit-transition: opacity 0.5s;
    -o-transition: opacity 0.5s;
    transition: opacity 0.5s;
}
.tipContent{
    color: black;
    background-color: white;
    border-radius: 5%;
    padding: 0.5% 2%;
}


.mainContainer{
    height: 100%;
    width: 100%;
    overflow-y: hidden;
    background: #343a40;
    color: #70e8f5;
    overflow-x:hidden;
    scroll-behavior: smooth;
}

.topNavigate{
    position: fixed;
    top: 4%;
    left: 2%;
    font-size: 1.4rem;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    white-space: nowrap;
    z-index: 10;
    cursor: pointer;
}

.fixContainer{
    position: fixed;
    top: 4%;
    left: 55%;
    width: 45%;
    height: 5%;
    z-index: 6;
}
.topNavigate, .fixContainer{
    color: white;
}

.topSearch{
    width: 45%;
    height: 100%;
    margin-right: 5%;
    display: inline-block;
    position: relative;
}

.searchInput, .searchButton{
    position: absolute;
    top: 0;
    height: 100%;
    font-size: 1rem;
    background-color: #343a40;
    border:0.07rem solid white;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}

.searchButton{
    left:88%;
    width: 12%;
    text-align: center;
    padding-top: 2%;
}

.searchButton:hover{
    color: white;
}

.searchInput{
    margin-right: -3%;
    width: 90%;
}

.searchInput:hover{
    border-color: white;
}

.topAvatar{
    margin-right: 7%;
    display: inline-block;
    margin-bottom: 3%;
}
.publish, .collectPub{
    cursor: pointer;
    font-size: 1rem;
}
.collectPub{
    margin-left: 2%;
}
.logout{
    width: 3%;
    margin-left: -7%;
    margin-right: 4%;
    cursor: pointer;
    font-size: 1rem;
    opacity: 0;
    -webkit-transition: opacity 0.5s;
    -o-transition: opacity 0.5s;
    transition: opacity 0.5s;
}

.topIndex{
    margin-left: 15%;
    cursor: pointer;
    position: relative;
}

#searchResult, #moreResult{
    position: fixed;
    top: 9.9%;
    left: 55%;
    width: 16.7%;
    height: 45%;
    border: 0.07rem solid #70e8f5;
    z-index: 0;
    background-color: #343a40;
    opacity: 0;
}
#moreResult{
    opacity: 1;
    scroll-behavior: smooth;
    overflow: hidden;
    z-index: 10;
}

#searchResult .lead, #moreResult .lead{
    width: 98%;
    margin-left: 4%;
}
#searchResult .more{
    margin-left: 65%;
}
#searchResult .cMore{
    margin-left: 58.5%;
}
#searchResult .more, #searchResult .cMore{
    cursor: pointer;
}
#searchResult .one, #searchResult .lead, #searchResult .uOne, #moreResult .one, #moreResult .lead, #moreResult .uOne{
    margin-top: 4%;
}
#searchResult .one, #moreResult .one{
    width: 100%;
    height: 10%;
}
#searchResult .one, #searchResult .uOne, #moreResult .one, #moreResult .uOne{
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    cursor: pointer;
}
#searchResult .one:hover, #searchResult .uOne:hover, #moreResult .one:hover, #moreResult .uOne:hover{
    background-color: grey;
}
#searchResult .cover, #moreResult .cover{
    margin-left: 10%;
    width: 10%;
}
#searchResult .name, #moreResult .name{
    margin-left: 10%;
}
#searchResult .uOne, #moreResult .uOne{
    width: 30%;
    height: 20%;
    margin-left: 10%;
    text-align: center;
    display: inline-block;
}
#searchResult .uCover, #moreResult .uCover{
    width: 40%;
    height: 50%;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
}

#loginContainer{
    position: fixed;
    top: -85%;
    left: 35%;
    width: 35%;
    height: 85%;
    border: 0.07rem solid #70e8f5;
    z-index: 15;
    background-color: #343a40;
    transition: top 0.6s, -webkit-transform 0.5s;
    -o-transition: top 0.6s, transform 0.5s;
    transition: top 0.6s, transform 0.5s;
    transition: top 0.6s, transform 0.5s, -webkit-transform 0.5s;
    -moz-transition: top 0.6s, -moz-transform 0.5s;	/* Firefox 4 */
    -webkit-transition: top 0.6s, -webkit-transform 0.5s;	/* Safari 和 Chrome */
    -o-transition: top 0.6s, -o-transform 0.5s;	/* Opera */
}
#loginContainer .closeLogin{
    position: absolute;
    right: 5%;
    top: 2%;
    font-size: 2rem;
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    -o-transition: transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    cursor: pointer;
}
#loginContainer .closeLogin:hover{
    transform: rotate(360deg);
    -ms-transform: rotate(360deg);		/* IE 9 */
    -webkit-transform: rotate(360deg);	/* Safari and Chrome */
    -o-transform: rotate(360deg);		/* Opera */
    -moz-transform: rotate(360deg);		/* Firefox */
}

#login .lead{
    display: block;
    font-size: 3rem;
    margin-top: 5%;
    margin-left: 30%;
}
#login .accountLead, #login .passwordLead, #login .account, #login .password, #login .login, #login .register{
    font-size: 1.5rem;
    margin-left: 10%;
}
#login .accountLead{
    margin-top: 5%;
}
#login .passwordLead{
    margin-top: 2%;
}
#login .account, #login .password, #login .login, #login .register{
    width: 80%;
}
#login .account, #login .password{
    font-size: 1rem;
    border: 0.07rem solid #343a40;
    border-bottom:0.07rem solid #70e8f5;
    background-color: #343a40;
}
#login .login, #login .register{
    font-size: 2rem;
    height: 12%;
    border: 0.07rem solid #70e8f5;
    display: block;
}
#login .login{
    background-color: #70e8f5;
    color: #343a40;
    margin-top: 15%;
}
#login .register{
    margin-top: 5%;
    background-color: #343a40;
}

#register{
    white-space: nowrap;
}
#register .avatarAndTip, #register .genderContainer, #register .signLead,
#register .sign, #register .nameLead, #register .IDLead, #register .submit{
    margin-left: 4%;
}
#register .avatarAndTip{
    width: 20%;
    height: 16.5%;
    overflow: visible;
}
#register .avatarAndTip, #register .accountAndPass, #register .lead{
    margin-top: 4%;
    display: inline-block;
}
#register .avatar{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-size: 100% 100%;
    margin-bottom: 13%;
}
#register .avatarInp{
    width: 100%;
    height: 100%;
    opacity: 0;
}
#register .tip{
    margin-left: -3%;
    cursor: pointer;
}
#register .accountAndPass{
    height: 25%;
    margin-left: 7%;
    width: 47%;
}
#register .passLead{
    margin-top: 7%;
}
#register .tip, #register .passLead, #register .accountLead, #register .genderLead,
#register .birthdayLead, #register .nicknameLead, #register .signLead{
    font-size: 1.1rem;
}
#register .account, #register .password{
    width: 100%;
    border: 0.07rem solid #343a40;
    border-bottom: 0.1rem solid #70e8f5;
    background-color: #343a40;
}
#register .lead{
    font-size: 1.8rem;
    margin-left: 6%;
    padding-left: 3%;
    border-left: 0.1rem solid #70e8f5;
}
#register .genderContainer, #register .birthdayLead, #register .birthday,
#register .signLead, #register .nameLead, #register .name, #register .phoneLead,
#register .phone, #register .IDLead, #register .ID, #register .submit,
#register .signLead, #register .nicknameContainer{
    margin-top: 3%;
}
#register .nicknameContainer{
    display: block;
    width: 100%;
    text-align: center;
}
#register .nickname{
    width: 60%;
    background-color: #343a40;
    border: 0.07rem solid #343a40;
    border-bottom: 0.1rem solid #70e8f5;
}
#register .genderContainer{
    height: 5%;
    width: 30%;
    display: inline-block;
    overflow: visible;
}
 #register .genderLead{
     margin-right: 7%;
 }
#register .gender{
    opacity: 0;
    width: 70%;
    background-color: #343a40;
}
#register .genderDisplay{
    display: inline-block;
    margin-left: -70%;
    height: 85%;
    border: 0.07rem solid #70e8f5;
    width: 50%;
    text-align: center;
    font-size: 1rem;
}
#register .downIcon{
    display: inline-block;
    border: 0.07rem solid #70e8f5;
    height: 85%;
    width: 20%;
    text-align: center;
    margin-left: -0.1%;
    font-size: 1rem;
}
#register .birthdayLead{
    margin-left: 7%;
}
#register .birthday{
    border: 0.07rem solid #343a40;
    border-bottom: 0.1rem solid #70e8f5;
    margin-left: 4%;
    background-color: #343a40;
    width: 37%;
}
#register .signLead{
    display: block;
}
#register .sign{
    width: 92%;
    height: 15%;
    background-color: #343a40;
    border: 0.07rem solid #70e8f5;
    resize: none;
}
#register .name, #register .phone, #register .ID{
    background-color: #343a40;
    border: 0.07rem solid #343a40;
    border-bottom: 0.1rem solid #70e8f5;
}
#register .name{
    width: 22%;
}
#register .phone{
    width: 43%;
}
#register .ID{
    width: 77%;
}
#register .submit{
    background-color: #343a40;
    width: 93%;
    height: 7%;
    border: 0.1rem solid #70e8f5;
}


